*::-webkit-scrollbar {
    display: none;
}

.work {
    position: relative;
    z-index: 2;
    background: rgb(201, 141, 31);
    padding-top: 20vh;
}

.header {
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    /* overflow: hidden; */
    margin: 10px auto;
}

.work .header {
    background-image: url(../about/images/airJump.jpg);
    background-position-x: center;
}

.topBanner {
    // height: 600px;
    width: 100%;
    // transform: skew(0deg, 5deg);
    // position: relative;
    // bottom: 350px;
    /* border-bottom: 100px solid #1a1919; */
    background: #2b7fb7;
}

.header h1 {
    font-size: 5rem;
    text-align: center;
    color: white;
    /* transform: skew(0deg, 5deg); */
    position: relative;
    /* top: -325px; */
}

.bottomBanner {
    height: 550px;
    width: 100%;
    border-top: 4px solid black;
    /* position: absolute; */
    bottom: -350px;
    /* transform: skew(0deg, 5deg); */
    background: #3e6dc9;
}

.work-container {
    width: 60%;
    border-radius: 8px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background: rgb(201, 141, 31);
}

.application {
    width: 48%;
    border: 2px solid black;
    border-radius: 8px;
    height: 35vh;
    position: relative;
    margin: 20px auto;
}

.application:first-child {
    width: 98%;
    margin: 0 auto;
    height: 60vh;
}

.application:first-child img {
    height: 60vh;
}

.description {
    background: white;
    max-width: 40%;
    position: absolute;
    bottom: 10px;
    left: 10px;
    padding: 5px;
    border: 2px solid black;
    border-radius: 8px;
    opacity: .65;
}

.description h3 {
    margin: 0 auto;
    width: fit-content;
}

.description p {
    width: fit-content;
    margin: 5px auto 10px 0;
    overflow: hidden;
    height: 80%;
    align-items: center;
    padding: 5px;
    border-radius: 8px;
}

.description button {
    width: 45%;
}

.application img {
    width: 100%;
    height: 35vh;
}



