@media screen and (max-width: 1500px){
    .row {
        width: 100%;
    }

    .header {
        height: 600px;
    }

    .topBanner {
        height: 275px;
        top: -100px;
    }

    .header h1 {
        top: -140px
    }

    .bottomBanner {
        height: 350px;
        bottom: -150px;
    }

    .contact .header h1 {
        bottom: 150px;
    }

}

@media screen and (max-width:768px) {
    
    .work-container {
        width: 100%;
        flex-direction: column;
    }

    .work .header {
        background-image: url(../about/images/platoon.jpg);
    }

    .header h1 {
        top: -122px;
        font-size: 17vmin;
    }

    .application:first-child, .application {
        width: 90%;
        height: auto;
    }

    .application:first-child img, .application img {
        height: auto;
    }
}